body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo-link {
  width: 40px;
  height: 40px;
}

.header-nav {
  display: flex;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  text-transform: uppercase;
  font-size: 12px;
}

.nav-link {
  color: inherit;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: inline-block;
}

.nav-link:hover {
  color: #00acdc;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

